import axios from "axios";
import React, { useState } from "react";
import { styled } from "styled-components";

const DeleteMember = () => {
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");

  const deleteEvent = async () => {
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/user/withdraw`,
      data: {
        userId: id,
        pw: pw,
      },
    })
      .then((res) => {
        if (res.data.boolean) {
          alert("회원탈퇴되었습니다.");
        } else {
          alert("입력정보를 확인해주세요.");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <Container>
      <Title>회원탈퇴</Title>
      <TextArea>
        <TextLine>회원탈퇴 시 일정, 게시글, 포인트, 할인쿠폰 등</TextLine>
        <TextLine>모든 정보가 삭제되며 복구를 못합니다.</TextLine>
      </TextArea>
      <InputArea>
        <LabelText>이메일</LabelText>
        <LabelText style={{ color: "#ff0000" }}>*</LabelText>
        <TextInput
          type="text"
          placeholder="abc@gamil.com"
          value={id || ""}
          onChange={(e) => setId(e.target.value)}
        />
        <Description>이메일 형식으로 입력해주세요.</Description>
        <LabelText>비밀번호</LabelText>
        <LabelText style={{ color: "#ff0000" }}>*</LabelText>
        <TextInput
          type="password"
          placeholder="비밀번호 입력"
          value={pw || ""}
          onChange={(e) => setPw(e.target.value)}
        />
        <Description>영문 대소문자, 숫자, 특수문자 포함한 8자리 이상 입력해주세요.</Description>
      </InputArea>
      <BtnArea>
        <DeleteBtn onClick={() => deleteEvent()}>탈퇴하기</DeleteBtn>
      </BtnArea>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  justify-content: center;
`;
const Title = styled.div`
  border-bottom: 1px solid #d9d9d9;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: #000;
  padding: 20px 0;
  display: flex;
  justify-content: center;
`;
const TextArea = styled.div`
  padding: 10px;
  margin-top: 60px;
  margin-bottom: 60px;
`;
const TextLine = styled.p`
  font-size: 16px;
  font-weight: 700;
  line-height: 29px;
  color: #000;
  margin-bottom: 10px;
  text-align: center;
`;
const InputArea = styled.div`
  margin: 0px 10px 80px 10px;
`;
const LabelText = styled.span`
  font-size: 14px;
  font-weight: 400;
  line-height: 20.27px;
  color: #000;
`;
const TextInput = styled.input`
  width: 100%;
  height: 30px;
  border: 0px;
  border-bottom: 1px solid #999999;
  font-size: 16px;
  font-weight: 500;
  line-height: 23.17px;
  color: #000;
`;
const Description = styled.p`
  font-size: 12px;
  font-weight: 400;
  line-height: 17.38px;
  color: #999;
  margin-top: 6px;
  margin-bottom: 16px;
`;
const BtnArea = styled.div`
  margin: 0px 10px;
  justify-content: center;
`;
const DeleteBtn = styled.button`
  width: 100%;
  height: 50px;
  border-radius: 8px;
  background-color: #25b1fa;
  font-size: 16px;
  font-weight: 500;
  line-height: 23.17px;
  color: #fff;
`;

export default DeleteMember;
